import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import * as All from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import firebase from '../config/Config';
import { useSelector, useDispatch } from 'react-redux';
import {
    setUsername,
    incrementByAmount,
    memberUsername,
} from '../features/memberProcess';
import GetUsername from '../Functions';

class MessageInput extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            messageList: [],
            message: "",
            username: this.props.Username
        };

        this.sendMessage = this.sendMessage.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
    }

    handleMessage (event) {
        this.setState({message: event.target.value});
    }

    sendMessage () {
        const refMessages = firebase.database().ref("messages");
        var messageList = this.state.messageList;

        refMessages.push({
            message: this.state.message,
            date: Date.now(),
            username: this.props.Username
        });

        this.setState({"messageList": messageList});
    }

    render () {
        return (
            <div className={'message-input'}>
                <div class="wrap">
                    <input value={this.state.message} onChange={this.handleMessage} type="text" placeholder="Write your message..." />
                    <button onClick={() => this.sendMessage()} className={'submit'}><FontAwesomeIcon icon={All.faPaperPlane} /></button>
                </div>
		    </div>
        )
    }
}

export default MessageInput;